@use "base/variables" as v;
@use "base/mixins" as m;

.sociales {
    margin: 5rem 0;
    display: flex;

    @include m.tablet {
        margin: 0;
    }

    &__enlace {
        &::before {
            content: "";
            padding: 2rem;
            display: inline-block;
        }
        &[href*="https://facebook.com"]::before
        {
            background-image: url(../img/icono_facebook.svg);
        }

        &[href*="https://twitter.com"]::before
        {
            background-image: url(../img/icono_twitter.svg);
        }

        &[href*="https://instagram.com"]::before
        {
            background-image: url(../img/icono_instagram.svg);
        }

        &[href*="https://pinterest.es"]::before
        {
            background-image: url(../img/icono_pinterest.svg);
        }

        &[href*="https://youtube.com"]::before
        {
            background-image: url(../img/icono_youtube.svg);
        }

        &[href*="https://tiktok.com"]::before
        {
            background-image: url(../img/icono_tiktok.svg);
        }
    }

    &__accesible {
        width: 1px;
        height: 1px;
        padding: 0;
        margin: -1px;
        overflow: hidden;
        clip: rect(0, 0, 0, 0);
        border: 0;
        display: block;

    }
}
