.btn {
  background-color: #ffffff;
  padding: 2rem 2rem;
  color: #000000;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 700;
  display: inline-block;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 0.3s;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}
.btn:hover {
  background-color: #e6e6e6;
}

.boton-negro {
  background-color: #000000;
  padding: 2rem 2rem;
  color: #ffffff;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 700;
  display: inline-block;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 0.3s;
}
@media (min-width: 768px) {
  .boton-negro {
    width: auto;
  }
}
.boton-negro:hover {
  background-color: black;
}

.header {
  padding-top: 5rem;
}
.header__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  background-color: #16b6a9;
  padding: 0.5rem 0;
  margin: 2rem 0;
}
.navegacion__border {
  border-block: 2px dashed #ffffff;
  padding: 1.7rem 0;
}
.navegacion__contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1.2rem;
}
@media (min-width: 768px) {
  .navegacion__contenedor {
    flex-direction: row;
    justify-content: space-between;
  }
}
.navegacion__enlace {
  color: #ffffff;
  font-size: 2.4rem;
}
.navegacion__enlace:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .navegacion__enlace {
    margin-bottom: 0;
  }
}

.sociales {
  margin: 5rem 0;
  display: flex;
}
@media (min-width: 768px) {
  .sociales {
    margin: 0;
  }
}
.sociales__enlace::before {
  content: "";
  padding: 2rem;
  display: inline-block;
}
.sociales__enlace[href*="https://facebook.com"]::before {
  background-image: url(../img/icono_facebook.svg);
}
.sociales__enlace[href*="https://twitter.com"]::before {
  background-image: url(../img/icono_twitter.svg);
}
.sociales__enlace[href*="https://instagram.com"]::before {
  background-image: url(../img/icono_instagram.svg);
}
.sociales__enlace[href*="https://pinterest.es"]::before {
  background-image: url(../img/icono_pinterest.svg);
}
.sociales__enlace[href*="https://youtube.com"]::before {
  background-image: url(../img/icono_youtube.svg);
}
.sociales__enlace[href*="https://tiktok.com"]::before {
  background-image: url(../img/icono_tiktok.svg);
}
.sociales__accesible {
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  display: block;
}

.bienvenida {
  text-align: center;
}
.bienvenida__heading {
  text-align: center;
  color: #ee90e5;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  font-size: 4rem;
}
.bienvenida__heading span {
  color: #000000;
  display: block;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}
.bienvenida__texto {
  font-size: 2.6rem;
  color: #979797;
  max-width: 50rem;
  margin: 4rem auto 0 auto;
  line-height: 1.8;
}

@media (min-width: 768px) {
  .cards__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.card {
  max-width: 35rem;
  margin: 0 auto 2rem auto;
}
.card:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .card {
    max-width: unset;
    margin: 0;
  }
}
.card__imagen {
  width: 100%;
  border: 0.5rem solid #ffffff;
  transition-property: transform;
  transition-duration: 0.3s;
}
.card__imagen:hover {
  transform: rotate(4deg) scale(1.3);
}
.card__contenido {
  max-width: 90%;
  margin: 0 auto;
  background-color: #ffffff;
  transform: translateY(-5rem);
  position: relative;
}
.card__heading {
  text-align: center;
  color: #ee90e5;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  font-size: 4rem;
  font-size: 3.4rem;
  padding: 2rem;
}
.card__heading span {
  color: #000000;
  display: block;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}
.card__enlace {
  background-color: #ee90e5;
  padding: 2rem 2rem;
  color: #ffffff;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 700;
  display: inline-block;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 0.3s;
  display: block;
}
@media (min-width: 768px) {
  .card__enlace {
    width: auto;
  }
}
.card__enlace:hover {
  background-color: #e764db;
}

.horario {
  background-image: linear-gradient(to bottom, #16b6a9 0%, #16b6a9 50%, transparent 50%), url(../img/11.jpg);
  background-repeat: no-repeat;
  background-position: center, bottom center;
  color: #ffffff;
}
@media (min-width: 768px) {
  .horario {
    background-image: linear-gradient(to right, #16b6a9 0%, #16b6a9 50%, transparent 50%), url(../img/11.jpg);
    background-position: center, center center;
    background-size: cover;
  }
}
.horario__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .horario__grid {
    grid-template-rows: unset;
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.horario__contenido {
  padding: 3rem 0;
}
.horario__heading {
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  font-size: 4rem;
}
.horario__texto {
  font-size: 2.2rem;
  line-height: 1.8;
  text-align: center;
}

.tabla {
  width: 100%;
  border: 0.5rem solid #ee90e5;
  text-align: center;
  border-collapse: collapse;
}
.tabla__thead {
  background-color: #ee90e5;
}
.tabla__th {
  padding: 1.5rem;
}
.tabla__tr:hover {
  background-color: #11897f;
}
.tabla__td {
  padding: 1rem;
  font-size: 2.2rem;
}

.productos__heading {
  text-align: center;
  color: #ee90e5;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  font-size: 4rem;
  margin-bottom: 5rem;
}
.productos__heading span {
  color: #000000;
  display: block;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}
.productos__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .productos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .productos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.producto__contenido {
  background-color: #ffffff;
  padding: 2rem;
}
.producto__imagen {
  width: 100%;
  transition-property: transform;
  transition-duration: 3s;
}
.producto__imagen:hover {
  transform: scale(1.3);
}
.producto__mask {
  overflow: hidden;
}
.producto__nombre {
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  font-size: 4rem;
  font-size: 2.4rem;
  color: #ee90e5;
}
.producto__descripcion {
  font-size: 1.6rem;
  text-transform: uppercase;
  line-height: 1.8;
  text-align: center;
}
.producto__precio {
  text-align: center;
  color: #979797;
  font-size: 2.8rem;
  margin: 0;
}