@use "variables" as v;

html {
    font-size: 62.5%; // 1rem = 10px;
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    background-color: v.$grisClaro;
    font-family: v.$fuentePrincipal;
    font-size: 1.6rem;
}

p {
    font-size: 2rem;
}

[class$="contenedor"],
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}

img {
    max-width: 100%;
}

ul {
    padding: 0;
    list-style: none;
    margin: 0;
}

.seccion {
    padding: 8rem 0;
}